
import {FBFieldWithPriceBaseOptions, PriceTypeEnum} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";
import {OptionItem} from "FormBuilder/Fields/Options/OptionItem";
import {StoreDataType} from "shared/core/Store/StoreBase";




export class FBDropDownOptions extends FBFieldWithPriceBaseOptions{
    public Placeholder:string;
    @StoreDataType(OptionItem)
    public Options:OptionItem[];


    protected LoadDefaultValues() {
        super.LoadDefaultValues();
        this.Options=[];
        this.Type=FieldTypeEnum.DropDown;
        this.Label='Dropdown';
        this.Placeholder='';
        this.PriceType=PriceTypeEnum.options;

        this.Options=[
            {Id:1, PriceType:PriceTypeEnum.fixed_amount,SalePrice:'',RegularPrice:'',Label:'Option 1'} as any,
            {Id:2, PriceType:PriceTypeEnum.fixed_amount,SalePrice:'',RegularPrice:'',Label:'Option 2'} as any,
            {Id:3, PriceType:PriceTypeEnum.fixed_amount,SalePrice:'',RegularPrice:'',Label:'Option 3'} as any
        ]


    }


}

